<template>
  <div class="question-card flex-column">
    <div class="question-card-header">
      {{ question.desc }}
    </div>
    <div class="question-card-body flex-row">
      <component
      :data="question"
      :reply="reply"
      :is="question.type"
      @complete="complete"
      />
    </div>
  </div>
</template>

<script>
import SingleChoice from './single-choice.vue';
import MultipleChoice from './multiple-choice.vue';
import BlankFilling from './blank-filling.vue';
import Opening from './opening.vue';

export default {
  components: {
    SingleChoice,
    MultipleChoice,
    Opening,
    BlankFilling,
  },
  props: {
    question: {
      type: Object,
      required: true,
    },
    reply: {
    },
  },
  methods: {
    complete(data) {
      this.$emit('complete', data);
    },
  },
};
</script>

