<template>
  <div class="flex-1 relative">
    <list
    :list="list"
    :total="total"
    :loading="loading"
    @click="toPage"
    @search="toSearch"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';
import formatDate from '@/tools/format-date';

export default {
  data() {
    return {
      results: [],
      total: 0,
      page: 1,
      keyword: '',
      loading: true,
    };
  },
  computed: {
    list() {
      return this.results.map(obj => ({
        content: `${obj.title} - ${obj.scores}/${obj.totalScores} - ${formatDate(obj.createdAt)}`,
        // eslint-disable-next-line
        to: `/results/${obj._id}`,
      }));
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    toSearch(keyword) {
      this.keyword = keyword;
      this.getData();
    },
    getData: debounce(function () {
      let user = localStorage.getItem('user');
      user = JSON.parse(user);
      if (user) {
        this.$axios.get(`
        ${this.$url.results}?username=${user.username}&page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.results = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch(console.log);
      }
    }, 300),
  },
};
</script>
