<template>
  <table class="mt-1 pure-table pure-table-horizontal w-100">
    <thead class="">
      <tr>
        <th>标题</th>
        <th>作者</th>
        <th>分類</th>
        <th>創建時間</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="card in cards" :key="card.title" class="pure-table-row">
        <td>
          <router-link :to="'/cards/' + card.title">
            {{ card.title }}
          </router-link>
        </td>
        <td>{{ card.author }}</td>
        <td>{{ card.category.toString() }}</td>
        <td>{{ card.createdAt | formatDateTime }}</td>
      </tr>
    </tbody>
  </table>
</template>
<script>
export default {
  props: {
    cards: {
      type: Array,
      required: true,
    }
  }
}
</script>

