<template>
  <div class="flex-row flex-1">
    <div class="left w-50">
      <form action="" class="w-100 flex-column pure-form pure-form-stacked">
        <label for="front">正面</label>
        <textarea class="w-100" name="front" id="front" cols="30" rows="10" required>
        </textarea>
        <label for="back">反面</label>
        <textarea class="w-100" name="back" id="back" cols="30" rows="10" required>
        </textarea>
        <button 
        class="pure-button"
        @click.prevent="createCard">Add Cards</button>
      </form>
      <div class="flex-row">
        <button 
        class="ml-auto pure-button"
        @click="toNext(-1)">Back</button>
        <button
        class="pure-button pure-button-primary"
        @click="toSave">Save & Next</button>
      </div>
    </div>
    <div class="right ml-1 flex-1">
      <table class="w-100 pure-table pure-table-strip">
        <caption>Cards</caption>
        <thead>
          <tr>
            <th>#</th>
            <th>Front</th>
            <th>Back</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(card, index) in cards" :key="card.front">
            <td>{{ index }}</td>
            <td>{{ card.front }}</td>
            <td>{{ card.back }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cards: [],
    }
  },
  mounted() {
    const cards = localStorage.getItem('polygon-cards-cards');
    if (cards) {
      this.cards = JSON.parse(cards);
    }
  },
  methods: {
    createCard() {
      const front = document.getElementById('front').value.trim();
      const back = document.getElementById('back').value.trim();
      if (front && back) {
        this.cards.push({
          front,
          back,
        })
      }
    },
    delCard() {
    },
    toNext(offset) {
      this.$emit('next', offset);
    },
    toSave() {
      localStorage.setItem('polygon-cards-cards', JSON.stringify(this.cards));
      this.toNext(1);
    }
  }
}
</script>

<style lang="scss" scoped>

.right {
  td {
    white-space: pre-line;
  }
}

</style>
