<template>
  <div class="flex-1 flex-row" v-loading="loading">
    <sidebar 
    v-if="isAuthor"
    :tabs="tabs"
    :active="active"
    @click="toClickTab"
    />
    <div class="flex-1 flex-column">

    <settings
      v-if="tabs[active] === 'settings' && !loading && isAuthor"
      :title="title"
      @save="toSave"
     />
     <generals
      v-if="tabs[active] === 'generals' && !loading"
      :test="test"
      @comment="toComment"
     />
     <submissions
      v-if="tabs[active] === 'submissions' && !loading"
     :title="title"
     />
    </div>
  </div>
</template>

<script>
import Settings from '../Polygon/panels/settings.vue';
import Generals from './General/index.vue';
import Submissions from './Submissions/index.vue';

export default {
  components: {
    Settings,
    Generals,
    Submissions,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      tabs: [
        'generals',
        'settings',
        'submissions',
      ],

      active: 0,

      loading: true,
      isAuthor: false,

      test: {},
      comment: {
        flag: false,
      },
      settings: {
        flag: false, // for settings
      },
    };
  },
  beforeRouteEnter(to, from, next) {
    const { title } = to.params;
    const jwt = localStorage.getItem('jwt');
    next((vm) => {
      vm.$axios.get(`${vm.$url.tests}/${title}`, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then((resp) => {
          // eslint-disable-next-line
          vm.test = resp.data;
          // eslint-disable-next-line
          vm.loading = false;
          if (window.$user) {
            if (vm.test.author === window.$user.username) {
              // eslint-disable-next-line
              vm.isAuthor = true;
              vm.tabs = [
                'generals',
                'settings',
                'submissions',
              ];
              window.localStorage.setItem(`${vm.title}-settings`, JSON.stringify(vm.test.settings));
            } else {
              vm.tabs = [
                // 'generals',
                // 'settings',
                // 'submissions',
              ];
            }
          }
        })
        .catch((err) => {
          vm.$alert('error', err.response.data);
          vm.$router.replace('/');
        });
    });
  },
  methods: {
    toClickTab(index) {
      this.active = index;
    },
    toComment(data) {
      if (this.comment.flag) {
        return;
      }
      this.comment.flag = true;
      this.test.comments.push(data);
      const jwt = localStorage.getItem('jwt');
      this.$axios.put(`${this.$url.tests}/${this.title}`, this.test, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then(() => {
          this.comment.flag = false;
        })
        .catch(() => {
          this.comment.flag = false;
        });
    },
    toSave(settings) {
      if (this.settings.flag) {
        return;
      }
      this.$axios.put(`${this.$url.tests}/${this.title}`, { settings })
        .then(() => {
          this.settings.flag = false;
        })
        .catch(() => {
          this.settings.flag = false;
        });
    },
  },
};
</script>

