<template>
  <div class="flex-1 flex-row">
    <form class="w-50 pure-form pure-form-stacked flex-column">
      <label for="type">Question Type</label>
      <select name="type" :value="type" id="type" @change="changeType($event)">
        <option
        :value="type.component" v-for="type in types"
        :key="type.name"
        >
          {{ type.name }}
        </option>
      </select>
      <label for="score" class="">
        Score
      </label>
      <input
        type="Number"
        min="0"
        id="score"
        name="score"
        placeholder="10"
        v-model="score"
      >
      <component
      style="flex: 1 0 auto;"
      :is="type"
      class="flex-1"
      @complete="toComplete"
      />
      <div class="ml-auto flex-row">
        <button
          class="pure-button" @click.prevent="next(-1)">
          Back
        </button>
        <button
          class="pure-button pure-button-primary margin-left-1" @click.prevent="next(1)">
          Save & Next
        </button>
      </div>
    </form>
    <ul class="flex-1 flex-column overflow-y" style="">
      <li class="margin-bottom-xs" v-for="(question, index) in questions" :key="question.desc">
        <question-item
          :index="index + 1"
          :data="question"
          @del="del($event, index)"
          @edit="edit($event, index)"
        >
        </question-item>
      </li>
    </ul>
  </div>
</template>

<script>
import QuestionItem from '../components/question-item.vue';
import SingleChoice from '../components/single-choice.vue';
import MultipleChoice from '../components/multiple-choice.vue';
import BlankFilling from '../components/blank-filling.vue';
import Opening from '../components/opening.vue';

export default {
  components: {
    QuestionItem,
    SingleChoice,
    MultipleChoice,
    BlankFilling,
    Opening,
  },
  data() {
    return {
      types: [
        {
          name: 'Single Choice',
          component: 'single-choice',
        },
        {
          name: 'Multiple Choice',
          component: 'multiple-choice',
        },
        {
          name: 'Blank Filling',
          component: 'blank-filling',
        },
        {
          name: 'Opening',
          component: 'opening',
        },
      ],


      editing: false,
      editingIndex: 0,

      loading: true,

      questions: [],
      type: 'single-choice',
      score: '',

    };
  },
  mounted() {
    const questions = localStorage.getItem('questions');
    if (questions) {
      this.questions = JSON.parse(questions);
    }
    this.loading = false;
  },
  methods: {
    toComplete(data) {
      const q = Object.assign({}, data, {
        type: this.type,
        score: this.score,
      });
      this.questions.push(q);
    },
    changeType(event) {
      this.type = event.target.value;
    },
    update() {
      // this.$set(this.questions, this.editingIndex, formData);
      // this.editing = false;
    },
    next(offset) {
      localStorage.setItem('questions', JSON.stringify(this.questions));
      this.$emit('next', offset);
    },
    del(data) {
      if (data && data.desc) {
        this.questions = this.questions.filter(q => q.desc !== data.desc);
        localStorage.setItem('questions', JSON.stringify(this.questions));
      }
    },
    edit(data, index) {
      this.editing = true;
      this.editingIndex = index;
      this.type = data.type;
      this.$nextTick(() => {
        switch (data.type) {
          case 'sc': {
            const elDesc = document.getElementById('sc-desc');
            elDesc.value = data.desc;
            this.singleChoiceQuestion.options = this.deepCopy(data.options);
            break;
          }
          case 'bf': {
            const elDesc = document.getElementById('bf-desc');
            const elAnswer = document.getElementById('bf-answer');
            elDesc.value = data.desc;
            elAnswer.value = data.answer;
            break;
          }
          default: {
            break;
          }
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>

.option label {
  margin: 0;
}

</style>

