<template>
  <div class="profile-tests-container flex-column flex-1">
    <form class="bg-2 pd-1 flex-row pure-form" @submit.prevent="">
      <input
      type="text"
      placeholder="Search"
      class="flex-item-center"
      v-model="keyword"
      >
      <button class="ml-auto flex-item-center pure-button pure-button-primary">
        <router-link to="/polygon/tests" style="color: white; text-decoration: none;">
          Create New Test
        </router-link>
      </button>
    </form>
    <div class="pd-1 flex-1 flex-column relative border-box" v-loading="loading">
      <no-data v-if="tests.length === 0 && !loading">
      </no-data>
      <ul v-else>
        <li class="mb-1" v-for="test in tests" :key="test._id">
          <router-link :to="'/tests/' + test.title">
            {{ test.title }}
          </router-link>
        </li>
      </ul>
    </div>
    <pager
    :total="total"
    @click="toPage"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';

export default {
  data() {
    return {
      tests: [],

      page: 1,
      total: 0,

      keyword: '',
      loading: true,
    };
  },
  watch: {
    keyword() {
      this.getData();
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    getData: debounce(function () {
      if (window.$user) {
        this.$axios.get(`${this.$url.tests}?username=${window.$user.username}&page=${this.page}&keyword=${this.keyword}`)
          .then((resp) => {
            this.tests = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch(console.log);
      }
    }, 300),
  },
};
</script>
