<template>
  <div class="polygon-container flex-row flex-1">
    <sidebar 
    :tabs="tabs"
    :status="status"
    :active="active"
    @click="toClickTab"
    />
    <!-- <ul class="nav-list shadow-1">
      <li class="nav-list-item" v-for="(tab, index) in tabs"
      @click="to(index)"
      :class="{'nav-list-item-active': active === index, 'nav-list-item-saved': status[index]}"
      :key="tab">
        {{ tab }}
      </li>
    </ul> -->
    <div class="polygon-body pd-1 flex-1 flex-column">
      <component
        :is="tabs[active]"
        @next='jumpTo'
      >
      </component>
    </div>
  </div>
</template>

<script>
import Generals from './components/generals.vue';
import Cards from './components/cards.vue';
import Publish from './components/publish.vue';

export default {
  components: {
    Generals,
    Cards,
    Publish,
  },
  data() {
    return {
      active: 0,
      tabs: [
        'generals',
        'cards',
        'publish',
      ],
      status: [
        false,
        false,
      ],
      counter: 0,
    };
  },
  methods: {
    toClickTab(index) {
      if (index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', 'you have unsaved forms.');
          return;
        }
      }
      this.active = index;
    },
    jumpTo(index) {
      if (index === 1) {
        if (!this.status[this.active]) {
          this.counter += 1;
        }
        this.status[this.active] = true;
      }
      if (this.active + index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', 'you have unsaved forms.');
          return;
        }
      }
      this.active += index;
    },
  },
};
</script>

<style lang="scss" scoped>
.polygon-container {
  position: relative;
}
.nav-link {
  text-decoration: none;
  color: #777;
}
.polygon-body {
  position: relative;
}
</style>

