<template>
  <textarea
    class="flex-1"
    v-model="localReply"
    >
  </textarea>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
    reply: {
      default: '',
    },
  },
  data() {
    return {
      localReply: this.reply,
    };
  },
  mounted() {
    this.$bus.$on('room-next', this.complete);
  },
  destroyed() {
    this.$bus.$off('room-next', this.complete);
  },
  methods: {
    complete(data) {
      this.$emit('complete', {
        reply: this.localReply,
        index: data.index,
        submit: data.submit,
      });
    },
  },
};
</script>
