<template>
  <div class="flex-column">
    <label for="desc">Description</label>
    <textarea
    name="desc"
    id="desc"
    cols="60"
    rows="5"
    v-model="question.desc"
    required
    ></textarea>
    <label for="option">Option</label>
    <textarea
    name="option"
    id="option"
    cols="60"
    rows="5"
    v-model="option"
    required>
    </textarea>
    <button
      class="pure-button"
      @click.prevent="addOption">
      Add Option
    </button>
    <hr>
    <span class="pure-form-message-inline">
      add options and check the correct answer.
    </span>
    <ul>
      <li v-for="(option, index) in question.options" :key="option">
        <div class="option flex-row">
          <radio
          name='options'
          :id="'option' + index"
          :message="option"
          @change="toChange($event, index)"
          />
        </div>
      </li>
    </ul>
    <button class="ml-auto pure-button pure-button-primary" @click.prevent="complete">Add Question</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: {
        options: [],
        desc: '',
        answer: -1,
      },
      option: '',
    };
  },
  methods: {
    addOption() {
      if (this.option.trim() !== '') {
        const trimedOption = this.option.trim();
        if (trimedOption) {
          this.question.options.push(trimedOption);
          this.option = '';
        }
      }
    },
    toChange(event, index) {
      if (event.target.checked) {
        this.question.answer = index;
      }
    },
    complete() {
      const trimedDesc = this.question.desc.trim();
      if (trimedDesc && this.question.index !== -1) {
        this.$emit('complete', this.question);
      }
    },
  },
};
</script>
