<template>
  <div class="flex-1 flex-column justify-center">
    <div v-if="success" class="flex-column flex-item-center">
      <p>发布成功</p>
      <p><a href="/">前往查看</a></p>
    </div>
    <div v-else class="flex-item-center">
        <button
        class="pure-button"
        @click="next(-1)">Back
      </button>
      <button class="margin-left-1 pure-button pure-button-primary" @click="togglePreview">Preview</button>
      <button class="margin-left-1 pure-button button-success" @click="publish">Publishs</button>
    </div>
    <preview v-if="preview" :test="paper" @close="preview = false"/>
  </div>
</template>

<script>
import axios from 'axios';
import Preview from '../components/preview.vue';

export default {
  components: {
    Preview,
  },
  data() {
    return {
      paper: {},
      preview: false,
      success: false,
    };
  },
  mounted() {
    const generals = JSON.parse(localStorage.getItem('generals'));
    const questions = JSON.parse(localStorage.getItem('questions'));
    const settings = JSON.parse(localStorage.getItem('settings'));
    const user = JSON.parse(localStorage.getItem('user'));
    this.paper = {
      title: generals.title,
      intro: generals.intro,
      tags: generals.tags,
      category: generals.category,
      author: user.username,
      // eslint-disable-next-line
      totalScore: questions.reduce((acc, cur) => acc += (+cur.score), 0),
      questions,
      settings,
    };
  },
  methods: {
    togglePreview() {
      this.preview = !this.preview;
    },
    next(offset) {
      this.$emit('next', offset);
    },
    publish() {
      axios.post(this.$url.tests, this.paper)
        .then(() => {
          this.success = true;
        })
        .catch(() => {
        });
    },
  },
};
</script>

