<template>
  <div class="flex-column">
    <label for="desc">Description</label>
    <textarea name="desc" id="desc" rows="5">
    </textarea>
    <label for="answer">Your Answer</label>
    <textarea name="answer" id="answer" rows="5">
    </textarea>
    <button class="ml-auto pure-button pure-button-primary" @click.prevent="complete">Add Question</button>
  </div>
</template>

<script>
export default {
  methods: {
    complete() {
      const descInput = document.getElementById('desc');
      const answerInput = document.getElementById('answer');
      const answer = answerInput.value.trim();
      const desc = descInput.value.trim();
      if (desc && answer) {
        answerInput.value = '';
        descInput.value = '';
        const question = {
          desc,
          answer,
        };
        this.$emit('complete', question);
      }
    },
  },
};
</script>

