<template>
  <div class="flex-1">
    <div
      v-for="i in data.answer.length"
      :key="i"
    >
      <input 
      :value="(reply && reply[i - 1]) ? reply[i - 1] : ''"
       class="pure-input replys" type="text" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
    reply: {
    },
  },
  mounted() {
    this.$bus.$on('room-next', this.complete);
  },
  destroyed() {
    this.$bus.$off('room-next', this.complete);
  },
  methods: {
    complete(data) {
      const reply = [];
      const els = document.getElementsByClassName('replys');
      for (let i = 0; i < els.length; i += 1) {
        reply.push(els[i].value.trim());
      }
      console.log('blank-filing', this.reply, reply);
      this.$emit('complete', {
        reply,
        index: data.index,
        submit: data.submit,
      });
    },
  },
}
</script>

