<template>
  <div class="cards pd-1" v-loading="loading"> 
    <div class="cards-header flex-column">
      <div class="ml-auto pure-button pure-button-primary">
        <router-link to="/polygon/cards">
          Create new Cards 
        </router-link>
      </div>
      <form 
      @submit.prevent=""
      class="pure-form mt-1"
      >
        <div class="flex-row">
          <input type="text" name="cards-search" id="cards-search"
            placeholder="search"
            class="ml-auto"
          >
          <select class="ml-1" v-model="view" name="cards-view" id="cards-view">
            <option value="table-view">table</option>
            <option value="grid-view">card</option>
          </select>
        </div>
      </form>
    </div>
      <component 
        class="mt-1"
        v-if="!loading"
        :cards="cards"
        :is="view"
      />
  </div>
</template>

<script>
import TableView from './components/table.vue';
import GridView from './components/grid.vue';

export default {
  components: {
    TableView,
    GridView,
  },
  data() {
    return {
      cards: [],
      view: 'table-view',
      loading: true,
    }
  },
  mounted() {
    this.$axios.get(this.$url.cards)
    .then((resp) => {
      this.cards = resp.data;
      this.cards = this.cards.concat(this.cards);
      this.cards = this.cards.concat(this.cards);
      this.cards = this.cards.concat(this.cards);
      this.cards = this.cards.concat(this.cards);
      this.cards = this.cards.concat(this.cards);
      this.cards = this.cards.concat(this.cards);
      this.loading = false;
    })
    .catch((err) => {
      console.log(err);
    })
  }
}
</script>

<style lang="scss" scoped>
</style>
