<template>
  <div class="flex-column">
    <label for="desc" class="">Description</label>
    <textarea name="desc" id="desc" cols="60" rows="5" v-model="question.desc"></textarea>
    <label for="option">Option</label>
    <textarea name="option" id="option" cols="60" rows="5" v-model="option"></textarea>
    <button
      class="pure-button"
      @click.prevent="addOption">
      Add Option
    </button>
    <hr>
    <span class="pure-form-message-inline">
      check the correct answer.
    </span>
    <ul>
      <li v-for="(option, index) in question.options" :key="option">
        <checkbox
        :checkbox-class="'options'"
        :id="'option-' + index"
        :message="option"
        />
      </li>
    </ul>
    <button class="ml-auto pure-button pure-button-primary" @click.prevent="complete">Add Question</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: {
        options: [],
        desc: '',
        answers: [],
      },
      option: '',
    };
  },
  methods: {
    addOption() {
      if (this.option.trim() !== '') {
        this.question.options.push(this.option);
        this.option = '';
      }
    },
    complete() {
      const els = document.getElementsByClassName('options');
      let hasAnswer = false;
      for (let i = 0; i < els.length; i += 1) {
        if (els[i].checked) {
          hasAnswer = true;
        }
        this.question.answers.push(els[i].checked);
      }
      if (!hasAnswer) {
        this.$alert('error', 'please select correct answers.');
        return;
      }
      this.$emit('complete', this.question);
    },
  },
};
</script>
