<template>
  <div class="polygon-container flex-1 flex-row h-100">
    <sidebar 
    :tabs="tabs"
    :status="status"
    :active="active"
    @click="toClickTab"
    />
    <div class="polygon-body pd-1 flex-1 flex-column">
      <component
        :is="tabs[active]"
        @next='jumpTo'
      >
      </component>
    </div>
  </div>
</template>

<script>
import Generals from '../panels/generals.vue';
import Questions from '../panels/questions.vue';
import Settings from '../panels/settings.vue';
import Publish from '../panels/publish.vue';

export default {
  components: {
    Generals,
    Questions,
    Settings,
    Publish,
  },
  data() {
    return {
      active: 0,
      tabs: [
        'generals',
        'questions',
        'settings',
        'publish',
      ],
      status: [
        false,
        false,
        false,
      ],
      counter: 0,
    };
  },
  methods: {
    toClickTab(index) {
      if (index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', 'you have unsaved forms.');
          return;
        }
      }
      this.active = index;
    },
    jumpTo(index) {
      if (index === 1) {
        if (!this.status[this.active]) {
          this.counter += 1;
        }
        this.$set(this.status, this.active, true);
      }
      if (this.active + index === this.tabs.length - 1) {
        if (this.counter !== this.status.length) {
          this.$alert('error', 'you have unsaved forms.');
          return;
        }
      }
      this.active += index;
    },
  },
};
</script>

<style lang="scss" scoped>

.polygon-container {
  position: relative;
}
.nav-link {
  text-decoration: none;
  color: #777;
}
.polygon-body {
  position: relative;
}
</style>

