<template>
  <div class="room-container flex-1 flex-column">
    <div v-if="loaded" class="flex-1 flex-column">
      <before-test
      v-if="preparing"
      :settings="test.settings"
      :length="test.questions.length"
      @start="start"
      />
      <after-test
      :id="result._id"
      v-else-if="end"
      />
      <div v-else>
        <div class="flex-row mt-1">
          <span @click="exit" class="ml-auto exit">
            <i @click="exit" class="fas fa-door-closed"></i>
            Exit
          </span>
        </div>
        <question-card
        class="mt-1"
        :question="test.questions[currentIndex]"
        :reply="replys[currentIndex]"
        @complete='toComplete'
        />
        <div class="flex-row mt-1">
          <button v-if="test.settings.timerMode === $constants.ALL" class="pure-button" @click="next(currentIndex - 1)">prev</button>
          <button class="pure-button" @click="next(currentIndex + 1)">next</button>
          <button
            style="margin-left: auto;"
            class="pure-button pure-button-primary"
            @click.prevent="toSubmit">Sumbit</button>
        </div>
        <footer v-if="test.settings.timerMode === $constants.ALL" class="flex-row mt-1">
          <ul class="pager flex-1">
            <li
            v-for="index in test.questions.length"
            class="pager-index"
            :id="'pager-' + index"
            :class="{active: currentIndex + 1 === index, done: replys[index - 1]}"
            :key="index"
            @click="next(index - 1)"
            >
              <span>{{ index }}</span>
            </li>
          </ul>
        </footer>
      </div>
    </div>
    <timer
    v-if="!preparing && !end"
    :total-time="remainningTime"
    :total-questions-cnt="10"
    :rest-questions-cnt="20"
    @timeout="toTimeout"
    @pong="toPong"
    />
  </div>
</template>

<script>
import BeforeTest from './components/before-test.vue';
import AfterTest from './components/after-test.vue';
import Timer from './components/timer.vue';
import QuestionCard from './components/question-card.vue';

export default {
  components: {
    BeforeTest,
    AfterTest,
    Timer,
    QuestionCard,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      test: {},

      remainningTime: 0,
      currentIndex: 1,

      replys: [],

      end: false,
      loaded: false,

      flag: false,
      result: {},
      preparing: true,

      timeUsed: 0,
      dataCollected: false,
    };
  },
  mounted() {
    let data = localStorage.getItem('test-session');
    if (data) {
      data = JSON.parse(data);
      this.test = data.test;
      this.remainningTime = data.remainningTime;
      this.currentIndex = data.currentIndex;
      this.replys = data.replys;
      this.loaded = true;
      this.preparing = data.preparing;
    } else {
      const jwt = localStorage.getItem('jwt');
      this.$axios
        .get(`${this.$url.tests}/${this.title}`, {
          withCredentials: true,
          headers: {
            Authorization: `Bearer ${jwt}`,
          },
        })
        .then((resp) => {
          this.test = resp.data;
          this.resetTimer();
          this.beginTime = Date.now();
          this.loaded = true;
        })
        .catch(() => {
          this.loaded = true;
        });
    }
  },

  methods: {
    toTimeout() {
      if (this.test.settings.timerMode === this.$constants.EACH) {
        if (this.currentIndex === this.test.questions.length - 1) {
          this.toSubmit();
        } else {
          this.currentIndex += 1;
          this.refreshCache();
          this.resetTimer();
        }
      } else if (this.test.settings.timerMode === this.$constants.ALL) {
        this.toSubmit();
      }
    },
    start() {
      this.resetTimer();
      this.preparing = false;
    },
    refreshCache() {
      const data = {
        remainningTime: this.remainningTime,
        currentIndex: this.currentIndex,
        replys: this.replys,
        preparing: this.preparing,
        test: this.test,
      };
      localStorage.setItem('test-session', JSON.stringify(data));
    },
    cleanCache() {
      localStorage.removeItem('test-session');
    },
    resetTimer() {
      const { minutes, seconds } = this.test.settings.timeLimit;
      this.remainningTime = (minutes * 60 * 1000) + (seconds * 1000);
    },
    next(index) {
      if (index < 0 || index >= this.test.questions.length) {
        return;
      }
      this.$bus.$emit('room-next', {
        index,
        submit: false,
      });
    },
    toComplete(data) {
      this.replys[this.currentIndex] = data.reply;
      if (data.submit) {
        if (this.timeUsed !== 0) {
          this.submit();
        } else {
          this.dataCollected = true;
        }
      } else {
        this.currentIndex = data.index;
        this.refreshCache();
        this.resetTimer();
      }
    },
    toPong(data) {
      this.timeUsed = data;
      if (this.dataCollected) {
        this.submit();
      }
    },
    toSubmit() {
      this.$bus.$emit('ping');
      this.$bus.$emit('room-next', {
        index: -1,
        submit: true,
      });
    },
    formatResult() {
      const user = window.$user;
      const result = {
        title: this.test.title,
        username: user.username,
        score: this.test.questions.reduce((acc, cur, index) => {
          let ok = false;
          if (cur.type === this.$TYPES.MULTIPLE_CHOICE) {
            if (this.replys[index].join('') === cur.answers.join('')) {
              ok = true;
            }
          }
          if (cur.type === this.$TYPES.SINGLE_CHOICE) {
            if (this.replys[index] === cur.answer) {
              ok = true;
            }
          }
          if (cur.type === this.$TYPES.OPENING) {
            if (this.replys[index] === cur.answer) {
              ok = true;
            }
          }
          if (ok) {
            // eslint-disable-next-line
            acc += cur.score;
          }
          return acc;
        }, 0),
        totalScore: this.test.totalScore,
        details: this.test.questions.map((q, index) => {
          if (!this.replys[index]) {
            return {
              question: q,
              reply: null,
              verdict: false,
            };
          }
          if (q.type === this.$TYPES.MULTIPLE_CHOICE) {
            return {
              question: q,
              reply: this.replys[index],
              verdict: this.replys[index].join('') === q.answers.join('') ? 0 : 1,
            };
          } else if (q.type === this.$TYPES.SINGLE_CHOICE) {
            return {
              question: q,
              reply: this.replys[index],
              verdict: this.replys[index] === q.answer ? 0 : 1,
            };
          } else if (q.type === this.$TYPES.OPENING) {
            return {
              question: q,
              reply: this.replys[index],
              verdict: this.replys[index] === q.answer ? 0 : 1,
            };
          } else  if (q.type === this.$TYPES.BLANK_FILLING) {
            return {
              question: q,
              reply: this.replys[index],
              verdict: this.replys[index].join('') === q.answer.join('') ? 0 : 1,
            };
          }
          return {};
        }),
      };
      return result;
    },
    submit() {
      if (this.flag) {
        return;
      }
      this.cleanCache();
      const result = this.formatResult();
      result.createdAt = Date.now();
      let timeLimit = (this.test.settings.timeLimit.minutes * 60 + this.test.settings.timeLimit.seconds) * 1000;
      result.timeUsed = this.timeUsed;
      result.timeLimit = timeLimit;
      debugger;
      const jwt = localStorage.getItem('jwt');
      this.flag = true;
      this.$axios
        .post(`${this.$url.results}`, result, {
          withCredentials: true,
          headers: {
            Authorization: `Bearer ${jwt}`,
          },
        })
        .then((resp) => {
          console.log(resp);
          this.flag = false;
          this.end = true;
          this.result = resp.data;
        })
        .catch((err) => {
          this.flag = false;
          this.$alert('error', err.message);
        });
    },
    exit() {
      this.$router.replace('/');
    },
    confirm() {
      return window.confirm('you will lose your changes, are you sure to exit?');
    },
  },
  destroyed() {
    this.cleanCache();
  },
  beforeRouteLeave(to, from, next) {
    if (this.confirm()) {
      this.cleanCache();
      this.$bus.$emit('changeMode', 'normal');
      window.$mode = 'normal';
      next();
    } else {
      next(false);
    }
  },
};
</script>

