<template>
  <div class="container">
    <div class="body flex-column">
      <div v-if="settings.schedule && !counting">
        <p>
          Test will be started at {{ `${settings.date} : ${settings.time}` }}.
        </p>
      </div>
      <div v-else>
        <p>
          There is {{ length }} questions.
        </p>
        <p v-if="settings.timerMode === $constants.EACH">
          Each quesition has time limit
          {{ `${settings.timeLimit.minutes ? settings.timeLimit.minutes +  'minutes' : ''}` }}
          {{ `${settings.timeLimit.seconds ? settings.timeLimit.seconds + ' seconds' : ''}` }}
        </p>
        <p v-else-if="settings.timerMode === $constants.ALL">
          Time Limit is
          {{ `${settings.timeLimit.minutes ? settings.timeLimit.minutes + ' minutes' : ''}` }}
          {{ `${settings.timeLimit.seconds ? settings.timeLimit.seconds + ' seconds' : ''}` }}
        </p>
        <p>
          Test will start in {{ remainingTime | clock }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    settings: {
      type: Object,
      required: true,
    },
    length: {
      type: Number,
    },
  },
  data() {
    return {
      remainingTime: 3000,
      counting: false,

      countdownHandler: null,
    };
  },
  watch: {
    remainingTime(value) {
      if (value === 0) {
        clearInterval(this.countdownHandler);
        this.$emit('start');
      }
    },
  },
  mounted() {
    if (this.settings.schedule) {
      if (Date.now() > this.settings.scheduleAt) {
        this.countdown();
      }
    } else {
      this.countdown();
    }
  },
  methods: {
    countdown() {
      this.counting = true;
      const unit = 1000;
      this.countdownHandler = setInterval(() => {
        this.remainingTime -= unit;
      }, unit);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/css/colors.scss";

.container {
  margin: auto;
  transform: translateY(-50%);
  font-size: 0.8em;
  color: $text-light;
}

</style>
