<template>
  <div class="cardview">
    <card-group 
    v-for="(card, index) in cards"
    :key="card.title + index"
    class="cardview-card"
    @click="toCard(card)"
    :data="card"
    >
    </card-group>
  </div>
</template>

<script>
export default {
  props: {
    cards: {
      type: Array,
      required: true,
    }
  },
  methods: {
    toCard(card) {
      this.$router.push('/cards/' + card.title);
    }
  }
}
</script>

<style lang="scss" scoped>
.cardview {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.8em;
  grid-auto-rows: minmax(100px, auto);
}

</style>
