<template>
  <form class="pure-form pure-form-stacked flex-column mg-auto" @submit.prevent="save"
  style="height: 600px;"
  >
      <div class="flex-row flex-1">
        <div class="left flex-column">
          <label for="username" class="form-group-label">Username</label>
          <input type="text" id="username" name="username" v-model="user.username" readonly>
          <label for="email" class="form-group-label">Email</label>
          <input type="email" id="email" name="email" v-model="user.email" readonly>
          <label for="bio" class="form-group-label">Bio</label>
          <textarea name="bio" id="bio" cols="30" rows="10" v-model="user.bio"></textarea>
        </div>
        <div class="right ml-1 flex-column flex-item-top">
          <img class="profile-avatar" :src="user.avatar" alt="avatar" id="avatar">
          <input type="file" name="avatar" @change="loadFile">
        </div>
      </div>
      <div class="flex-item-center">
        <button @click.prevent="resetUser" class="pure-button">Reset</button>
        <button class="pure-button pure-button-primary margin-left-1">Save</button>
      </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      user: {},
      flag: false,
    };
  },
  mounted() {
    this.resetUser();
  },
  methods: {
    resetUser() {
      this.user = JSON.parse(JSON.stringify(window.$user));
    },
    save(event) {
      if (this.flag) {
        return;
      }
      const form = event.target;
      const formData = new FormData(form);
      const jwt = localStorage.getItem('jwt');
      this.flag = true;
      this.$axios.put(this.$url.users, formData, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then((resp) => {
          this.flag = false;
          this.user = resp.data;
          const newUser = Object.assign(JSON.parse(localStorage.getItem('user')), this.user);
          localStorage.setItem('user', JSON.stringify(newUser));
          window.$user = newUser;
          // eslint-disable-next-line
          window.location = location;
        })
        .catch((err) => {
          this.$alert('error', err.message);
          this.flag = false;
        });
    },
    loadFile(event) {
      const el = event.target;
      if (el.files && el.files[0]) {
        const reader = new FileReader();
        reader.onload = (e) => {
          document.getElementById('avatar').setAttribute('src', e.target.result);
        };
        reader.readAsDataURL(el.files[0]);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.right {
  max-width: 200px;
}

.profile-avatar {
  width: 10em;
  height: 10em;
  object-fit: cover;
  border-radius: 50%;
}

</style>
