<template>
<div class="width flex-row flex-1 pd-1">
  <form class="width-50 flex-column pure-form pure-form-stacked">
    <div class="form-group">
      <span>Accessbility</span>
      <label for="public" class="pure-radio">
        <input type="radio" id="public" name="accessbility" :value=0 v-model="settings.accessbility">
        Public
      </label>
      <label for="private" class="pure-radio">
        <input type="radio" id="private" name="accessbility" :value=1 v-model="settings.accessbility">
        Private
      </label>
      <span v-if="settings.accessbility === 0" class="pure-form-message">
        any user can access this test.
      </span>
      <span v-else class="pure-form-message">
        only users in white list has permisssion.
      </span>
    </div>
    <div class="form-group">
      <label class="" for="">Time Limit</label>
      <input type="number" v-model="settings.timeLimit.minutes">
      <span class="pure-form-message">
        minutes
      </span>
      <input type="number" v-model="settings.timeLimit.seconds">
      <span class="pure-form-message">
        Seconds
      </span>
    </div>
    <div class="form-group">
      <span>Timer Mode</span>
        <label for="timerMode1">
          <input type="radio" name="timerMode" id="timerMode1" v-model="settings.timerMode" :value=0>
          Each Question
        </label>
        <label for="timerMode2" class="margin-left-1">
          <input type="radio" name="timerMode" id="timerMode2" v-model="settings.timerMode" :value=1>
          All Question
        </label>
        <span v-if="settings.timerMode === 0" class="pure-form-message">
          Time Limit is set for each question,
          automatically jump to next question when timeout.
        </span>
        <span v-else class="pure-form-message">
          Time Limit is set for all questions.
          Paticipants can choose questions.
        </span>
    </div>
    <div class="form-group">
      <span>Schedule</span>
      <label for="schedule-on">
        <input type="radio" name="schedule" id="schedule-on" v-model="settings.schedule" :value=0>
        off
      </label>
      <label for="schedule-off" class="margin-left-1">
        <input type="radio" name="schedule" id="schedule-off" v-model="settings.schedule" :value=1>
        on
      </label>
      <span v-if="settings.openMode === 1" class="pure-form-message">
        test awalys opened.
      </span>
      <span v-else class="pure-form-message">
        test can only be accessd on schedulted time.
      </span>
    </div>
    <div v-if="settings.schedule" class="form-group">
      <span class="form-group-label">Date & Time</span>
      <div class="pure-g">
        <input type="date" class="" id="scheduleDate" required v-model="settings.date">
        <input type="time" class="" id="scheduleTime" required v-model="settings.time">
      </div>
    </div>
    <div class="ml-auto margin-top-auto flex-row">
      <button v-if="!title" class="ml-1 pure-button" @click.prevent="next(-1)">Back</button>
      <button @click.prevent="reset" class="ml-1 pure-button">Reset</button>
      <button class="ml-1 pure-button pure-button-primary" @click.prevent="save">
        {{ this.title ? 'Save' : 'Save & Next' }}
      </button>
      <button v-if="this.title" class="ml-1 pure-button button-error" @click.prevent="del">
        Delete
      </button>
    </div>
  </form>
  <div v-if="settings.accessbility === 1" class="background-gray flex-1 flex-column whitelist">
    <div class="flex-row pure-form whitelist-header">
      <input type="text" id="whitelist-search" placeholder="Search" v-model="keyword">
      <label for="whitelist-selectall" style="margin-left: auto;">
        Select All
        <input type="checkbox" id="whitelist-selectall" @change="selectAll">
      </label>
      <button class="pure-button pure-button-primary ml-1" @click.prevent="sendMail">Send Invite Mail</button>
    </div>
    <div class="whitelist-body">
      <div class="header flex-row">
        <form class="ml-auto margin-top-1 pure-form">
          <div class="pure-control-group">
            <input type="email" placeholder="Email" id="email">
            <button @click.prevent="addEmail" class="pure-button margin-left-1">+ Add</button>
          </div>
        </form>
      </div>
      <ul class="margin-top-1 overflow-y">
        <li class="flex-row mt-1" v-for="email in filterdMails" :key="email">
          <label class="flex-1  flex-item-center" style="margin-left: 0.6em" >
            <input type="checkbox" class="mail-checkbox">
            {{ email }}
          </label>
          <button class="pure-button button-error" @click.prevent="delEmail(email)">delete</button>
        </li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
    },
  },
  computed: {
    filterdMails() {
      if (!this.mailKeyword) {
        return this.settings.whitelist;
      }
      const reg = new RegExp(this.mailKeyword);
      return this.settings.whitelist.filter(mail => reg.test(mail));
    },
  },
  data() {
    return {
      settings: {
        accessbility: 0,
        timeLimit: {
          minutes: 0,
          seconds: 10,
        },
        timerMode: 0,
        whitelist: [],
        schedule: 0,
        scheduleAt: 0,
        date: 0,
        time: 0,
      },
      keyword: '',
    };
  },
  mounted() {
    let settings;
    if (this.title) {
      settings = JSON.parse(localStorage.getItem(`${this.title}-settings`));
    } else {
      settings = JSON.parse(localStorage.getItem('settings'));
    }
    if (settings) {
      this.settings = settings;
    }
  },
  methods: {
    selectAll(event) {
      const el = event.target;
      if (el.checked) {
        const els = document.getElementsByClassName('mail-checkbox');
        for (let i = 0; i < els.length; i += 1) {
          els[i].checked = true;
        }
      } else {
        const els = document.getElementsByClassName('mail-checkbox');
        for (let i = 0; i < els.length; i += 1) {
          els[i].checked = false;
        }
      }
    },
    save() {
      if (this.settings.schedule) {
        const date = document.getElementById('scheduleDate');
        const time = document.getElementById('scheduleTime');
        if (date.checkValidity() && time.checkValidity()) {
          const dateTime = `${date.value} ${time.value}`;
          const timeStamp = new Date(dateTime).getTime();
          this.settings.scheduleAt = timeStamp;
        }
      }
      if (this.title) {
        localStorage.setItem(`${this.title}-settings`, JSON.stringify(this.settings));
        this.$emit('save', this.settings);
      } else {
        localStorage.setItem('settings', JSON.stringify(this.settings));
      }
      this.next(1);
    },
    next(offset) {
      this.$emit('next', offset);
    },
    reset() {
      const settings = JSON.parse(JSON.stringify(this.data));
      if (settings) {
        this.settings = settings;
      }
      // TODO:
      // set settings to default.
    },
    sendMail() {
      this.$axios.post(`${this.$url.sendmail}/turing%20test`)
        .then(() => {
          this.$alert('success', 'send mail success.');
        })
        .catch(console.log);
    },
    addEmail() {
      const email = document.getElementById('email');
      if (email.checkValidity()) {
        this.settings.whitelist.push(email.value);
      } else {
        const btn = document.createElement('button');
        email.appendChild(btn);
        btn.click();
        email.removeChild(btn);
      }
    },
    delEmail(data) {
      this.settings.whitelist = this.settings.whitelist.filter(email => email !== data);
    },
    del() {
      const jwt = localStorage.getItem('jwt');
      this.$axios.delete(`${this.$url.tests}/${this.title}`, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then((resp) => {
          if (resp.data.ok) {
            this.$alert('success', 'Test Deleted.');
            setTimeout(() => {
              this.$router.replace('/');
            }, 300);
          }
        })
        .catch(console.log);
    },
  },
};
</script>

<style lang="scss" scoped>

.whitelist {
  border: 1px dashed silver;
  margin-left: 0.4em;
  padding: 0.6em 0.8em;
  align-self: flex-start;
  // min-height: 20em;
  overflow-y: auto;
}

.whitelist-header {
  background: #eee;
  * {
    align-self: center;
  }
}

.whitelist-body {
  ul {
    max-height: 50em;
  }
  li:hover {
    background: #ccc;
  }
}
</style>
