<template>
  <div class="meta-info" @click="toggleMeta">
    <div v-if="!mini" class="meta-detail" >
      <dl>
        <dt>
          剩余时间
        </dt>
        <dd>
          {{ remainingTime | clock}}
        </dd>
        <dt>
          剩餘題數
        </dt>
        <dd>
          {{ restQuestionsCnt }}
        </dd>
        <dt> 縂題數 </dt>
        <dd> {{ totalQuestionsCnt }} </dd>
      </dl>
    </div>
    <span v-else class="meta-default">
      {{ remainingTime | clock }}
    </span>
  </div>
</template>

<script>
export default {
  props: {
    totalTime: {
      type: Number,
    },
    totalQuestionsCnt: {
      type: Number,
    },
    restQuestionsCnt: {
      type: Number,
    },
  },
  data() {
    return {
      remainingTime: 0,
      handler: null,
      mini: true,
      beginTime: 0,
    };
  },
  mounted() {
    const unit = 1000;
    this.remainingTime = this.totalTime;
    const fn = () => {
      this.remainingTime -= unit;
      if (this.remainingTime <= 0) {
        clearTimeout(this.handler);
        this.handler = null;
        this.$emit('timeout', Date.now() - this.beginTime);
      } else {
        this.handler = setTimeout(fn, unit);
      }
    };
    this.beginTime = Date.now();
    setTimeout(fn, unit);

    this.$bus.$on('ping', this.ping)
  },
  destroyed() {
    this.$bus.$off('ping', this.ping)
  },
  methods: {
    ping() {
      this.$emit('pong', Date.now() - this.beginTime);
    },
    toggleMeta() {
      if (!this.mini) {
        document.getElementsByClassName('meta-detail')[0].classList +=
          ' slideOut';
        setTimeout(() => {
          this.mini = true;
        }, 300);
      } else {
        this.mini = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.meta-info {
  position: fixed;
  color: #777;
  right: 0px;
  top: 10em;
  width: 50px;
  padding: 0.4em 0.6em;
}
.meta-default {
  border: 1px solid silver;
  padding: 0.4em 0.6em;
  border-radius: 2px;
}
@keyframes slideIn {
  from {
    right: -100px;
    width: 0px;
  }
  to {
    right: 150px;
    width: 200px;
  }
}
@keyframes slideOut {
  from {
    right: 150px;
    width: 200px;
  }
  to {
    right: -100px;
    width: 0px;
  }
}
.meta-detail {
  margin: 0px;
  position: relative;
  background: white;
  animation: slideIn 0.3s;
  border: 1px solid silver;
  padding-left: 0.6em;
  width: 200px;
  height: 200px;
  right: 150px;
}
.slideOut {
  animation: slideOut 0.3s;
}
</style>
