<template>
  <div class="flex-row flex-1 h-100" >
    <div class="left flex-column shadow-1">
      <div class="pd-1">
        <img class="profile-avatar" :src="user.avatar" alt="avatar">
        <div class="profile-item">
          {{ user.username }}
        </div>
        <div class="profile-item">
          {{ user.email }}
        </div>
        <p class="profile-item">
          {{ user.bio }}
        </p>
      </div>
      <ul class="nav-list">
        <li
          class="profile-tab pd-1"
          v-for="tab in tabs" :key="tab"
          :class="{'profile-tab-active': active === tab}"
          @click="active = tab"
        >
          {{ tab }}
        </li>
      </ul>
    </div>
    <component
    :is="active"
    >
    </component>
  </div>
</template>

<script>
import Activities from './Activities/index.vue';
import Tests from './Tests/index.vue';
import Results from './Results/index.vue';
import Comments from './Comments/index.vue';
import Settings from './Settings/index.vue';

export default {
  components: {
    Tests,
    Results,
    Comments,
    Activities,
    Settings,
  },
  data() {
    return {
      user: {},
      loading: true,

      active: 'Tests',
      tabs: [
        'Tests',
        'Results',
        'Comments',
        'Activities',
        'Settings',
      ],
    };
  },

  mounted() {
    if (window.$user) {
      this.user = window.$user;
      this.loading = false;
    } else {
      this.alert('error', 'please login first.');
      this.$router.push('/login');
    }
  },
};
</script>

