<script>
export default {
  props: {
    test: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      vote: {
        disable: false,
        count: 0,
      },
    };
  },
  methods: {
    toComment(data) {
      this.$emit('comment', data);
    },
    toVote(count) {
      this.test.vote.disable = true;
      this.test.vote.count = count;
      const jwt = localStorage.getItem('jwt');
      this.$axios.put(`${this.$url.stars}/${this.test.title}`, { count }, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then((resp) => {
          console.log(resp);
        })
        .catch(console.log);
    },
  },
};
