<template>
  <div class="card-group pd-1 flex-column" v-loading="loading">
    <template v-if="!loading">
    <div class="flex-row">
      <h1>{{ data.title }}</h1>
      <button 
        class="
        pure-button pure-button-primary ml-auto
        flex-item-center
        ">
        <a :href="'/room/pratice/' + data.title" target="_blank">
          start test
        </a>
      </button>
    </div>
    <div class="flex-row">
      <img
        src="/logo.png" alt=""
        width="200px"
        height="200px"
        style="object-fit: cover;"
      >
      <ul class="ml-1 flex-1 meta-list">
        <li class="meta-list-item">
          <span class="meta-label">作者</span>
          <span class="meta-value">{{ data.author }}</span>
        </li>
        <li class="meta-list-item">
          <span class="meta-label">發佈日期</span>
          <span class="meta-value">{{ data.createdAt | formatDateTime }}</span>
        </li>
        <li class="meta-list-item">
          <span class="meta-label">目錄</span>
          <span class="meta-value">
            {{ data.category.toString() }}
          </span>
          <!-- <breadcrumb 
          class="meta-value"
          :data="data.category"
          :readonly="true"
          /> -->
        </li>
        <li class="meta-list-item">
          <span class="meta-label">评价</span>
          <span>
            <star
              class="meta-value"
              :disable="data.vote.disable"
              :count="6 - data.vote.count"
              @click="toVote"
            />
          </span>
        </li>
        <li class="meta-list-item">
          <span class="meta-label">標簽</span>
          <tag 
          class="meta-value"
          v-for="tag in data.tags"
          :text="tag"
          :key="tag"
          />
        </li>
        <li class="meta-list-item">
          <span class="meta-label">卡片數量</span>
          <span class="meta-value">{{ data.cards.length }}</span>
        </li>
      </ul>
      <rating 
      :data="data.rating"
      />
    </div>
    <div class="mt-1">
      {{ data.intro ?  data.intro : '' }}
    </div>
    <my-comment 
    class="mt-1"
    :data="data.comments"
    @submit="toComment"
    />
      </template>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      data: '',
      loading: true,
    }
  },
  mounted() {
    this.$axios.get(`${this.$url.cards}/${this.title}`)
    .then((resp) => {
      this.data = resp.data;
      console.log(this.data);
      this.loading = false;
    })
  },
  methods: {
    toComment(data) {
      this.data.comments.push(data)
      this.$axios.put(`${this.$url.cards}/${this.title}`, this.data)
      .then(() => {})
    },
    toVote(count) {
      this.data.vote.disable = true;
      this.data.vote.count = count;
      const jwt = localStorage.getItem('jwt');
      this.$axios.put(`${this.$url.voteCards}/${this.data.title}`, { count }, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${jwt}`,
        },
      })
        .then((resp) => {
        })
        .catch(console.log);
    }
  }
}
</script>

<style lang="scss" scoped>

.card-group {
  width: 600px;
  margin: auto;
}

.meta-label {
  vertical-align: top;
  color: #777;
  display: inline-block;
  text-align: left;
  font-size: 0.8em;
  position: relative;
  vertical-align: top;
  &::after {
    content: ":";
  }
}
.meta-value {
  font-size: 0.8em;
  vertical-align: top;
  color: #555;
  display: inline-block;
  width: 8em;
  margin-left: 0.4em;
}

.meta-list-item:nth-child(n + 1) {
  margin-top: 0.4em;
}
</style>
