<template>
  <div class="flex-column">
    <label for="desc">Description</label>
    <textarea name="desc" id="desc" cols="30" rows="5"></textarea>
    <span class="pure-form-message">
      Use ##...## to insert blank and answer.
      <br>
      Example:
      <br>
     "1 + 1 = ##2## ?"
      <br>
      =>
      <br>
      "1 + 1 = ___ ?"
    </span>
    <button class="ml-auto pure-button pure-button-primary" @click.prevent="complete">Add Question</button>
  </div>
</template>

<script>
import translate from '@/tools/translate';

export default {
  methods: {
    complete() {
      const desc = document.getElementById('desc').value;
      if (desc.trim() === '') {
        return;
      }
      const result = translate(desc);
      const question = {
        desc: result.desc,
        answer: result.answer,
      };
      this.$emit('complete', question);
    },
  },
};
</script>
