<template>
  <div class="profile-comments-container flex-column flex-1">
    <list
    :list="list"
    :total="total"
    :loading="loading"
    @click="toPage"
    @search="toSearch"
    />
  </div>
</template>

<script>
import debounce from '@/tools/debounce';
import formatDate from '@/tools/format-date';

export default {
  data() {
    return {
      data: [],
      page: 1,
      total: 0,

      keyword: '',
      loading: true,
    };
  },
  computed: {
    list() {
      return this.data.map(obj => ({
        content: `${`${obj.content.substr(0, 12)}...`} - ${obj.title} - ${formatDate(obj.createdAt)}`,
        to: `/tests/${obj.title}`,
      }));
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    toSearch(keyword) {
      this.keyword = keyword;
      this.getData();
    },
    getData: debounce(function () {
      const user = window.$user;
      const jwt = localStorage.getItem('jwt');
      if (user) {
        this.$axios.get(
          `${this.$url.comments}?username=${user.username}&page=${this.page}&keyword=${this.keyword}`
          , {
            withCredentials: true,
            headers: {
              Authorization: `Bearer ${jwt}`,
            },
          },
        )
          .then((resp) => {
            this.data = resp.data.results;
            this.total = resp.data.total;
            this.loading = false;
          })
          .catch((err) => {
            console.log(err);
          });
      }
    }, 300),
  },
};
</script>
